import React from "react";
import "./App.css";
import "./Navbar.css";
import Sidebar from "./Components/Sidebar";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Navbar from "./Components/Navbar";
import Home from "./Components/Home";
import Search from "./Components/Search";
import Library from "./Components/Library";
import Prodcasts from "./Components/Prodcasts";
import Artists from "./Components/Artists";
import Albums from "./Components/Albums";
import LikedSongs from "./Components/Liked Songs";
import CreatePlaylist from "./Components/Create Playlists";
function App() {
  return (
    <Router>
      <div className="App">
        <Sidebar />
        <Route path="/" component={Home} exact />
        <Route path="/search" component={Search} />
        <Route path="/collection/playlists" component={Library} />
        <Navbar />
        <Route path="/collection/prodcasts" component={Prodcasts} />
        <Route path="/collection/artists" component={Artists} />
        <Route path="/collection/albums" component={Albums} />
        <Route path="/collection/tracks" component={LikedSongs} />
        <Route path="/create" component={CreatePlaylist} />
        <Route path="/account" component={Home} />
      </div>
    </Router>
  );
}
export default App;
