import React from "react";
import { Link } from "react-router-dom";

const Prodcasts = () => {
  return (
    <div>
      <h1 className="prodcasts-page">Podcasts you’ve liked live here</h1>
      <div className="head2">
        <h6>Looking for something new?</h6>
        <Link id="prod-icon">PRODCASTS</Link>
      </div>
    </div>
  );
};
export default Prodcasts;
