import React from "react";
import { Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import { Avatar } from "antd";

class Navbar extends React.Component {
  render() {
    function myFunction() {
      document.getElementById("myDropdown").classList.toggle("dropdown");
    }
    if (window.location.href === "http://localhost:3000/search") {
      return (
        <div className="Nav">
          <div className="main-nav">
            <li>
              <Link to="/" className="previous-btn prev">
                &#8249;
              </Link>
            </li>
            <li>
              <Link to="/" className="next-btn next">
                &#8250;
              </Link>
            </li>
            <li>
              <div class="container h-100">
                <div class="d-flex justify-content-center h-100">
                  <div class="searchbar">
                    <input
                      class="search_input"
                      type="text"
                      name=""
                      placeholder="Search for Artists, Songs, or Prodcasts "
                    />
                    <i class="search_icon">
                      <i class="fas fa-search"></i>
                    </i>
                  </div>
                </div>
              </div>
            </li>
            <li className="nav-item dropdown">
              <div class="dropdown">
                <button class="dropbtn" onClick={myFunction}>
                  <Avatar src="./sultan.png" /> Mohamed Ahmed
                  <i class="fa fa-caret-down"></i>
                </button>
                <div id="myDropdown" class="dropdown-content">
                  <a href="http://localhost:3000/account">Account</a>
                  <a href="http://localhost:3000/signin">Logout</a>
                </div>
              </div>
            </li>
          </div>
        </div>
      );
    }
    if (window.location.href === "http://localhost:3000/collection/playlists") {
      return (
        <div className="Nav">
          <ul className="main-nav">
            <li>
              <Link to="/" className="previous-btn prev">
                &#8249;
              </Link>
            </li>
            <li>
              <Link to="/" className="next-btn next">
                &#8250;
              </Link>
            </li>

            <li>
              <Link to="/collection/playlists" id="playlist-icon">
                Playlists
              </Link>
            </li>
            <li>
              <Link to="/collection/prodcasts" id="prodcast-icon">
                Prodcasts
              </Link>
            </li>
            <li>
              <Link to="/collection/artists" id="artists-icon">
                Artists
              </Link>
            </li>
            <li>
              <Link to="/collection/albums" id="albums-icon">
                Albums
              </Link>
            </li>
            <li className="nav-item dropdown">
              <div class="dropdown">
                <button class="dropbtn" onClick={myFunction}>
                  <Avatar src="./sultan.png" /> Mohamed Ahmed
                  <i class="fa fa-caret-down"></i>
                </button>
                <div id="myDropdown" class="dropdown-content">
                  <a href="/account">Account</a>
                  <a href="/signin">Logout</a>
                </div>
              </div>
            </li>
          </ul>
        </div>
      );
    }
    if (window.location.href === "http://localhost:3000/login") {
      return (
        <div className="Nav">
          <ul className="main-nav">
            <li>
              <Link to="/" className="previous-btn prev">
                &#8249;
              </Link>
            </li>
            <li>
              <Link to="/" className="next-btn next">
                &#8250;
              </Link>
            </li>
            <li>
              <Link to="login" id="login-icon">
                LOG IN
              </Link>
            </li>
            <li>
              <Link to="login" id="signUp-icon">
                SIGN UP
              </Link>
            </li>
          </ul>
        </div>
      );
    }

    if (window.location.href === "http://localhost:3000/") {
      return (
        <div className="Nav">
          <ul className="main-nav">
            <li>
              <Link to="/" className="previous-btn prev">
                &#8249;
              </Link>
            </li>
            <li>
              <Link to="/" className="next-btn next">
                &#8250;
              </Link>
            </li>
            <li>
              <Link to="upgrade" id="upgrade-icon">
                UPGRADE
              </Link>
            </li>
            <li className="nav-item dropdown">
              <div class="dropdown">
                <button class="dropbtn" onClick={myFunction}>
                  <Avatar src="./sultan.png" /> Mohamed Ahmed
                  <i class="fa fa-caret-down"></i>
                </button>
                <div id="myDropdown" class="dropdown-content">
                  <a href="account">Account</a>
                  <a href="signin">Logout</a>
                </div>
              </div>
            </li>
          </ul>
        </div>
      );
    }
    if (window.location.href === "http://localhost:3000/collection/tracks") {
      return <div className="Nav" style={{ background: "white" }}></div>;
    }
    if (window.location.href === "http://localhost:3000/create") {
      return <div className="Nav" style={{ background: "white" }}></div>;
    } else {
      return <div></div>;
    }
  }
}
export default Navbar;
