import React from "react";
import { Redirect } from "react-router-dom";

class Sidebar extends React.Component {
  redirect = () => {
    return <Redirect to="/search" />;
  };

  render() {
    if (
      window.location.href === "http://localhost:3000/" ||
      window.location.href === "http://localhost:3000/collection/playlists" ||
      window.location.href === "http://localhost:3000/search" ||
      window.location.href === "http://localhost:3000/create" ||
      window.location.href === "http://localhost:3000/collection/tracks" ||
      window.location.href === "http://localhost:3000/account"
    ) {
      return (
        <div className="side">
          <a href="/">
            <img
              className="logo"
              src="./spotifylogo.png"
              alt="logo"
              width="auto"
              height="40"
            />
          </a>
          <a href="/" id="home-icon" className="fas fa-home">
            Home
          </a>
          <a
            href="/search"
            id="search-icon"
            className="fas fa-search"
            onClick={this.redirect}
          >
            Search
          </a>
          <a href="/collection/playlists" id="lib-icon" className="fas">
            &#xf025; Your Library
          </a>
          <h6 className="text">PLAYLISTS</h6>
          <a href="/create" id="create-icon" className="fas fa-plus">
            Create Playlist
          </a>
          <a href="/collection/tracks" id="liked-icon" className="fas fa-heart">
            Liked Songs
          </a>
        </div>
      );
    } else {
      return (
        <div style={{ textAlign: "center", color: "red", padding: "100px" }}>
          <h1>WARNINNG</h1>
          <h3>ERROR PATH NOT FOUND</h3>
        </div>
      );
    }
  }
}
export default Sidebar;
